

const _errorImage = require("~/assets/images/errorwifi.png");
const _errorExecution = require("~/assets/images/error_execution.png");
const _errorMic = require("~/assets/images/error_mic.png");
const _errorNoise = require("~/assets/images/error_noise.png");
const _errorWifi = require("~/assets/images/error_wifi.png");
const _error10014 = require("~/assets/images/error_10014.png");
const _error10016 = require("~/assets/images/error_10016.png");
const _error10019 = require("~/assets/images/error_10019.png");
const maxTries = 5;

export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    error: {
      type: String,
      default: "error_restapi"
    },
    tries: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      phase: 1,
      warnpopup: false
    }
  },
  computed: {
    environment: function() {
      if($nuxt.$config.env.environment.indexOf('library') > -1) {
        return "library";
      }
      return "app";
    },
    showDialog: function () {
      if (this.tries >= maxTries) {
        this.phase = 2;
      }
      return this.show;
    },
    canDownloadAudio() {
      if (this.error == "error_restapi") {
        return true
      }
      return false
    },
    errorImage() {
      if (this.error == "error_restapi") {
        return _errorWifi;
      }
      if (this.error == "incorrect_execution") {
        return _errorExecution;
      }
      if (this.error == "no_audio") {
        return _errorMic;
      }
      if (this.error == "too_noise") {
        return _errorNoise;
      }
      if (this.error == "error_10014") {
        return _error10014;
      }
      if (this.error == "error_10016") {
        return _error10016;
      }
      if (this.error == "error_10019") {
        return _error10019;
      }
      if (this.error == "error_10020") {
        return _error10019;
      }
      return _errorImage;
    },
    errorTitle() {
      if (this.error == "error_10014" || this.error == "error_10016" || this.error == "error_10019"|| this.error == "error_10020") {
        return 'completitionfailed.' + this.error + '.title';
      }
      if (this.error == "error_restapi" || this.error == "incorrect_execution" || this.error == "no_audio" || this.error == "too_noise") {
        return 'completitionfailed.' + this.error + '.title';
      }
      return 'completitionfailed.title_' + this.phase;
    },
    errorMessage() {
      if (this.error == "error_10014" || this.error == "error_10016" || this.error == "error_10019"|| this.error == "error_10020") {
        return 'completitionfailed.' + this.error + '.message';
      }
      if (this.error == "error_restapi" || this.error == "incorrect_execution" || this.error == "no_audio" || this.error == "too_noise") {
        return 'completitionfailed.' + this.error + '.message';
    }
      return 'completitionfailed.msg_' + this.phase;
    }

  },
  methods: {
    requestDownload(e) {
      console.log('some one requests a download');
      this.$emit('blobdownload', e);
    },
    closeClick(e) {
      //Home loose every thing, get back to home
      if (this.phase === 1) {
        //show popup sample
        this.warnpopup = true;
        return;
      }
      this.closeAnyWay(e);
    },
    closeAnyWay(e) {
      this.$emit('close', e);
      $nuxt.$router.replace($nuxt.localePath("/home"));
    },
    nextClick(e) {
      this.$emit('retry', e);
    }

  },
  mounted() {

  }
}
